*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Manrope", sans-serif;
}

.card__heading {
  font-size: clamp(0.75rem, 2.5vw, 0.875rem);
  letter-spacing: 0.313em;
  text-transform: uppercase;
  color: hsl(150deg, 100%, 66%);
}

.card__advice {
  font-size: clamp(1.5rem, 5vw, 1.75rem);
  color: hsl(193deg, 38%, 86%);
}

.attribution {
  font-size: 0.875rem;
  line-height: 2em;
  color: hsl(193deg, 38%, 86%);
}

.card {
  width: min(100% - 2rem, 33.75rem);
  position: relative;
  text-align: center;
  background-color: hsl(217deg, 19%, 24%);
  border-radius: 0.625rem;
  box-shadow: 0 2.5rem 1.875rem -1.875rem rgba(0, 0, 0, 0.15);
  padding: clamp(2.625rem, 7.5vw, 3.125rem) clamp(1.5rem, 5vw, 2.875rem) clamp(4rem, 10vw, 4.5rem);
  margin-inline: auto;
}
.card__advice {
  padding-block: clamp(1.75rem, 5vw, 1.875rem) clamp(1.5rem, 6vw, 2.5rem);
}
.card__divider {
  width: min(100%, 27.75rem);
  height: 1rem;
  display: block;
  margin-inline: auto;
  object-fit: cover;
}
.card__button {
  width: 4rem;
  height: 4rem;
  display: grid;
  place-items: center;
  position: absolute;
  bottom: -2rem;
  left: 50%;
  background-color: hsl(150deg, 100%, 66%);
  border: none;
  border-radius: 50%;
  transform: translateX(-50%);
  transition: box-shadow 300ms;
  cursor: pointer;
}
.card__button:hover, .card__button:focus-visible {
  outline: none;
  box-shadow: 0 0 1.875rem hsl(150deg, 100%, 66%);
}

.attribution {
  text-align: center;
  padding: 1rem;
}
.attribution a {
  text-decoration: none;
  color: inherit;
  transition: color 200ms;
}
.attribution a:hover, .attribution a:focus-visible {
  color: hsl(150deg, 100%, 66%);
}

.fa-heart {
  color: hsl(150deg, 100%, 66%);
  margin-inline: 0.25em;
  animation: heart-pulse 2000ms linear infinite;
}
@keyframes heart-pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

body {
  min-height: 100vh;
  display: grid;
  place-items: center;
  grid-template-rows: 1fr min-content;
  background-color: hsl(218deg, 23%, 16%);
}